.overlay {
  position: fixed;
  top: 0%;
  left: 0%;
  width: 100vw;
  height: 0%;
  background-color: #fea052;
  opacity: 0;
  visibility: hidden;
  overflow: hidden;
  transition: opacity 0.35s, visibility 0.35s, height 0.35s;
}
.overlay.is-active {
  opacity: 1;
  visibility: visible;
  height: 100vh;
}
.overlay.is-active .overlay__item {
  animation: fadeInRight 0.5s ease forwards;
  animation-delay: 0.35s;
}
.overlay.is-active .overlay__item:nth-of-type(2) {
  animation-delay: 0.4s;
}
.overlay.is-active .overlay__item:nth-of-type(3) {
  animation-delay: 0.45s;
}
.overlay.is-active .overlay__item:nth-of-type(4) {
  animation-delay: 0.5s;
}
.overlay__item {
  display: block;
  height: 15%;
  min-height: 50px;
  position: relative;
  opacity: 0;
}
.overlay__link {
  display: block;
  position: relative;
  color: #fff;
  font-size: 15px;
  font-weight: 700;
  text-decoration: none;
  overflow: hidden;
  padding-bottom: 10px;
  text-transform: uppercase;
}
