.overlay__link:focus:after,
.overlay__link:active:after {
  width: 100%;
}
.overlay__link:after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 0%;
  transform: translateX(-50%);
  height: 3px;
  background: #fff;
  transition: 0.35s;
}
